@media screen and (max-width:576px) {

    .section-one {
        display: flex;
        flex-direction: column-reverse;
        padding: 20% 5% 10% 5%;
    }

    .s-one-d-one {
        width: 100% !important;
    }

    .s-one-d-two {
        width: 100% !important;
    }
    .s-one-text{
        line-height: 1.5;
        font-size: 5vw;
    }
    .section-one-button {
        border: none;
        border-radius: 5px;
        color: #fff;
        background-color: #3508AA;
        padding: 1% 2%;
        margin: 4% 0;
        font-size: 6vw;
    }
    
    .s-one-d-one h2 {
        font-size: 10vw;
        margin: 10% 0;
    }

    .font-large{
        font-size: 6vw;
    }

    .font-small {
        font-size: 4vw;
    }

    .font-medium {
        font-size: 5vw;
    }
/* Section Two */
.section-two{
    margin: 10% 5% 10% 5%;
}
.s-two-div{
    display: flex;
    flex-direction: column;
    margin: 5% 0%;
}
.s-two-d-one{
    width: 100%;
    margin: 5% 0;
}
.s-two-icon{
    font-size: 12vw;
}
    /* Section Three */
    .section-three {
        background-color: #3508AA;
        padding: 5% 5%;
        color: #fff;
    }

    .s-three-d-one-img {
        width: 100%;
        margin-top: 1%;
    }

    .s-three-d-two-img {
        margin-top: 1%;
        width: 49.5%;
    }

    /* Section Five  */
    .section-five {
        padding: 5% 5%;
        background-color: #3508AA;
    }

    .s-five-d {
        width: 25%;
        text-align: center;
    }

    .s-six-icon {
        width: 10%;
        border-radius: 50%;
        border: 1px solid #3508AA;
    }

    .numbers{
        font-size: 10vw !important;
    }

    /* Section Five  */
    .section-five {
        padding: 5% 5%;
        background-color: #3508AA;
    }

    .s-five-d {
        width: 100%;
        text-align: center;
        margin-top:5%;
    }
    .testimonial-container {
        display: flex;
      flex-direction: column;
    }
    
    .testimonial {
        background-color: #fff;
        margin: 5% 0 0 0;
        padding: 20px;
        border-radius: 8px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        text-align: center;
        max-width: 100%;
    }
    /* ---------------------------------------- Footer */


}