.section-one {
    padding: 10% 5% 5% 5%;
}

.s-one-d-one {
    width: 50%;
}

.s-one-d-one h2 {
    font-size: 3.5vw;
    margin: 4% 0;
}

.s-one-text{
    line-height: 1.5;
    font-size: 1.5vw;
    text-align: justify;
}

.color-primary {
    color: #3508AA;
}

.font-small {
    font-size: 1.5vw;
}

.font-medium{
    font-size: 2.5vw;
    font-weight: 500;
}
.numbers{
    font-size: 5vw;
}

.section-one-button {
    border: none;
    border-radius: 5px;
    color: #fff;
    background-color: #3508AA;
    padding: 1% 2%;
    margin: 4% 0;
    font-size: 1.5vw;
}

.s-one-d-two {
    width: 50%;
}

.section-img-div {
    padding: 5%;
    width: 50%;
    background-color: #3508AA;
    transform: rotate(-10deg);
    margin: 0% auto;

}

.section-one-img {
    width: 100%;
    transform: rotate(10deg);

}

/* Section Two */
.section-two{
    margin: 5% 5% 10% 5%;
}
.s-two-div{
    margin: 5% 0%;
}
.s-two-d-one{
    width: 30%;
    background-color: #F5FEFD;
    padding: 2.5%;

}
.s-two-icon{
    font-size: 5vw;
}

/* Section Three */
.section-three{
    background-color: #3508AA;
    padding: 5% 5%;
    color: #fff;
}
.s-three-d-one-img{
    width: 49.5%;
}
.s-three-d-two-img{
    margin-top:1% ;
    width: 24.3%;
}
/* Section Four */
.section-three{
    padding: 5% 5%;
    color: #fff;
}
/* Section Five  */
.section-five{
    padding: 5% 5%;
    background-color: #3508AA;
}
.s-five-d{
    width: 25%;
    text-align: center;
}
.s-six-icon{
    width: 10%;
    border-radius: 50%;
    border: 1px solid #3508AA;
}
/* Section Six  */
.section-six{
    padding: 5% 5%;
}
.s-six-d-card{
    padding: 2%;
    width: 30%;
    border: 2px solid #000;
    border-radius: 5px;
}

.testimonial-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.testimonial {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    max-width: 30%;
}

.quote {
    font-style: italic;
}

.author {
    margin-top: 10px;
    font-weight: bold;
}

/* ---------------------------------------- Footer */
footer{
    padding: 5% 5% 0% 5% !important;
}
.contact-icon{
    font-size: 1.6vw !important;
    margin: auto;
    color: gray;
}
.text-gray{
    color: gray;
}
.text-gray:hover {
    color: #cccccc;
}

