footer {
    padding: 5% 5% 0% 5% !important;
    background-color: #282A35;
}

.contact-icon {
    font-size: 1.6vw !important;
    margin: auto;
    color: gray;
}

.text-gray {
    color: gray;
}

.text-gray:hover {
    color: #cccccc;
}

/* ---------------------------------------- Social-Icon */
.social-icon {
    font-size: 3.5vw;
    margin: 1.5vw;
    color: gray;
}

.social-icon-w {
    font-size: 3.5vw;
    color: gray;
}

.social-icon:hover {
    color: #cccccc;
}


@media screen and (max-width:576px) {
    footer div {
        margin: 0% auto;

    }

    .footer-space {
        padding: 5% 0;
    }

    .social-icon-div icon {
        margin: auto 0% !important;
    }

    .contact-icon {
        font-size: 5vw !important;
    }

    .social-icon {
        font-size: 10vw;
        margin: 2vw;
    }

    .social-icon-w {
        font-size: 10vw;
        color: gray;
    }
}